import { useState } from "react";
import Modal from "react-modal";
import {
  HomeOutlined,
  ArrowLeftOutlined,
  ArrowRightOutlined,
  MenuOutlined,
  LineOutlined,
  CloseOutlined,
} from "@ant-design/icons";
Modal.setAppElement("#root");

function App() {
  const [isOpen, setIsOpen] = useState(false);

  const TIME_ELEMENTS = [
    ["7:00 ~ 8:00", "집합", "QT", "QT", "QT"],
    ["8:00 ~ 9:00", "아침식사", "아침식사", "아침식사"],
    ["9:00 ~ 10:00", "가지모임(2)", "가지모임(3)", "폐회 예배"],
    ["10:00 ~ 11:00", "활동(1)", "선택 특강", "받은 은혜 가지고 살아내기"],
    ["11:00 ~ 12:00"],
    ["12:00 ~ 14:00", "점심식사", "점심식사"],
    ["14:00 ~ 15:00", "오프닝 콘서트", "내연산 등산", "활동(2)"],
    ["15:00 ~ 16:00", "QT & 휴식"],
    ["16:00 ~ 17:30", "가지 모임(1)"],
    ["17:30 ~ 19:00", "저녁식사"],
    ["19:00 ~ 21:30", "저녁집회"],
    ["21:30 ~ 22:00", "가지별 모임", "가지별 모임", "지구별 모임"],
    ["22:00 ~ ", "취침 (월, 화: 12시 소등)"],
  ];

  function goHompage() {
    window.location.href = "http://www.dfc.kr/";
  }

  function openModal() {
    setIsOpen(true);
  }

  function closeModal() {
    setIsOpen(false);
  }

  return (
    <div id="main">
      <Modal isOpen={isOpen} contentLabel="히브리서 10장 25절" id="verse-modal">
        <p>
          모이기를 폐하는 어떤 사람들의 습관과 같이 하지 말고 오직 권하여 그
          날이 가까움을 볼수록 더욱 그리하자 <br /> 히브리서 10장 25절
        </p>
        <label htmlFor="modal-close-button">
          <button onClick={closeModal} id="modal-close-button">
            닫기
          </button>
        </label>
      </Modal>
      <div id="top">
        <div className="window-controll">
          <div className="window-controll-div">
            {/* <LineOutlined />
            <CloseOutlined /> */}
          </div>
        </div>
        <div className="search-bar">
          <ArrowLeftOutlined />
          <ArrowRightOutlined />
          <HomeOutlined />
          <p>www.대구포항 DFC 22 겨울수련회.co.kr</p>
          <MenuOutlined />
        </div>
      </div>
      <div id="home">
        <div className="time-table-div">
          <div className="time-table">
            <div className="time-table-top">
              <p>❤ 만날 결심 ❤</p>
            </div>
            <table>
              {/* HEAD */}
              <thead>
                <tr className="point">
                  <th></th>
                  <th>첫째 날</th>
                  <th>둘째 날</th>
                  <th>셋째 날</th>
                  <th>넷째 날</th>
                </tr>
              </thead>

              {/* BODY */}
              <tbody>
                {TIME_ELEMENTS.map((times, idx) => (
                  <tr key={idx}>
                    {times.map((time, i) => {
                      return time.includes("~") ? (
                        <td className="point" key={i}>
                          {time}
                        </td>
                      ) : time === "집합" ? (
                        <td rowSpan={6} key={i}>
                          {time}
                        </td>
                      ) : time === "활동(1)" ? (
                        <td rowSpan={2} key={i}>
                          {time}
                        </td>
                      ) : time === "활동(2)" ? (
                        <td rowSpan={3} key={i}>
                          {time}
                        </td>
                      ) : time.includes("특강") ? (
                        <td rowSpan={2} key={i}>
                          {time}
                        </td>
                      ) : time === "내연산 등산" ? (
                        <td rowSpan={3} key={i}>
                          {time}
                        </td>
                      ) : time === "받은 은혜 가지고 살아내기" ? (
                        <td rowSpan={10} key={i}>
                          {time}
                        </td>
                      ) : time.includes("저녁") ? (
                        <td colSpan={3} key={i}>
                          {time}
                        </td>
                      ) : time.includes("취침") ? (
                        <td colSpan={3} key={i}>
                          {time}
                        </td>
                      ) : (
                        <td key={i}>{time}</td>
                      );
                    })}
                  </tr>
                ))}
              </tbody>
            </table>
            <p className="temp">* 둘째 날 전체 특강은 15:30분까지 입니다!</p>
          </div>
        </div>
      </div>
      <div id="bottom">
        <button onClick={goHompage} className="hompage-button">
          Be DFC GROWTH
        </button>
        <button onClick={openModal} className="modal-button">
          <p>히브리서 10:25</p>
        </button>
      </div>
    </div>
  );
}

export default App;
